import axios from 'axios';
import { message } from 'antd';
import { GETNEWTOKEN } from './index';
import { logoutNoRequest } from '@/action/settingAction';
import { configureStore } from '@/store/configureStore'

// token
export const getToken = () => axios.defaults.headers.common['authorization'] = window.localStorage.getItem('token');
 
// cookie / 
axios.defaults.withCredentials = true;

// token
const setNewToken = token => {
  window.localStorage.setItem('token', token);
}

// ,token
let isRefreshing = false;

// token
let requestsArr = [];

//  token
// axios.interceptors.request.use(req => {  
//   CHECKTOKENEFFECTIVE
// })

// 
//  - token TODO: axios
const setupAxiosInterceptors = onUnauthenticated => {

  axios.interceptors.response.use(res => {
    const config = res.config;
    
    // 5999refreshtokenrefreshToken
    if (res && (res.data.status === 5998 || res.data.status === 5999)) {
      // 
      if (!config.url.includes('signout')) {
        message.warning('refreshToken,');
      }else{
        onUnauthenticated();
        return new Promise((resolve, reject) => {
          resolve({
            data: {
              status: 1002,
              message: 'refreshToken,'
            }
          });
        })
      }
    }

    if (res && res.data.status === 401 && localStorage.getItem('refreshToken')) {
      const refreshToken = localStorage.getItem('refreshToken');

      if (!isRefreshing) {
        isRefreshing = true;
        return new Promise((resolve, reject) => {
          GETNEWTOKEN(refreshToken).then(res => {
            const { token } = res.data;
            setNewToken(token);
            config.headers['authorization'] = token;
            // token
            requestsArr.forEach(cb => cb(token))
            requestsArr = [];
            axios(config).then(res => {
              if (res && res.status === 200) {
                resolve(res);
              }
            })
          }).catch(err => {
            message.error(err);
          }).finally(() => {
            isRefreshing = false;
          })
        })
      }
    } else {
      return res
    }
  })
}

const { dispatch } = configureStore();
setupAxiosInterceptors(jumpUrl => {
  dispatch(logoutNoRequest(jumpUrl));
})

/**
 * @description get
 * @param url       
 * @param msg       
 * @param headers   header
 */

export const get = ({ url, msg = '', headers }) =>
  axios
    .get(url, headers)
    .then(res => res.data)
    .catch(msg => message.warning(msg));

/**
 * @description post
 * @param url       
 * @param data      
 * @param msg       
 * @param headers   header
 */
export const post = ({ url, data, msg = '', headers }) =>
  axios
    .post(url, data, headers)
    .then(res => res.data)
    .catch(msg => message.warning(msg));