/**
 * @description 
 * @method {GET}
 * @param {username}
 */
export const getLoginAuth = (username) => {
  return get({
    url: config.GETLOGINAUTH + `?username=${username}`
  })
}

/** 
 * @description  
 * @method {POST}
 */
export const handleLogin = (username, password) => {
  getToken();
  return post({
    url: config.LOGIN_URL,
    data: qs.stringify({
      username: username,
      password: password,
    })
  });
};

